@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nationItem {
  display: flex;
  flex-direction: row;
  width: 90%;
  margin: 8px auto;
  align-items: center;
  justify-content: space-between;
}

.onTop {
  padding: 20px 30px;
  border: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  width: 90% !important;
  justify-content: space-around;
}

.inforNation {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.index {
  padding: 8px 12px;
  background-color: #fff;
}

.flagNation {
  margin-right: 20px;
}

.nameContainer {
  display: flex;
  flex-direction: column;
}

.nameNation {
  font-weight: bold;
  font-size: 16px;
}
.lastUpdate {
  font-weight: 600;
  color: rgb(190, 165, 165);
}

.caseNation {
  color: rgb(145, 61, 61);
  font-weight: bold;
  float: right;
}

.main {
  width: 30vw;
  min-height: 100vh;
  background-color: #ddd;
  margin: 0 auto;
  transition: all 0.2s;
}

.infor {
  padding: 50px;
}
.totalCase {
  font-size: 40px;
  color: red;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  /* font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif; */
  font-weight: bold;
  text-align: center;
}

.deathsAndRecover {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
}

.deaths {
  font-size: 26px;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.recovered {
  font-size: 26px;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.label {
  font-size: 12px;
  color: rgb(175, 150, 150);
}

.inputContainer {
  width: 90%;
  height: 50px;
  border: 1px solid #ccc;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  align-self: center;
  margin: auto;
  border-radius: 12px;
  margin: auto;
}

.image {
  margin-right: 30px;
}
.input {
  outline: none;
  width: 70%;
  border: none;
}

::-webkit-input-placeholder {
  color: #ccc;
  font-weight: 500;
  font-size: 16px;
}

:-ms-input-placeholder {
  color: #ccc;
  font-weight: 500;
  font-size: 16px;
}

::-ms-input-placeholder {
  color: #ccc;
  font-weight: 500;
  font-size: 16px;
}

::placeholder {
  color: #ccc;
  font-weight: 500;
  font-size: 16px;
}

.listNation {
  width: 100%;
  height: auto;
}

@media (max-width: 1200px) {
  .main {
    width: 40vw;
  }
}

@media (max-width: 1024px) {
  .main {
    width: 60vw;
  }
}

@media (max-width: 768px) {
  .main {
    width: 60vw;
  }
}

@media (max-width: 640px) {
  .main {
    width: 80vw;
  }
}

@media (max-width: 446px) {
  .main {
    width: 100vw;
  }
  .totalCase {
    font-size: 32px;
  }
  .deaths {
    font-size: 16px;
  }
  .recovered {
    font-size: 16px;
  }
}

@media (max-width: 320px) {
  .main {
    width: 100vw;
  }
}

:root {
  --primary: #08aeea;
  --secondary: #13d2b8;
  --purple: #bd93f9;
  --pink: #ff6bcb;
  --blue: #8be9fd;
  --gray: #333;
  --font: "Poppins", sans-serif;
  --gradient: linear-gradient(40deg, #ff6ec4, #7873f5);
  --shadow: 0 0 15px 0 rgba(0, 0, 0, 0.05);
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}

body {
  font-family: "Poppins", sans-serif;
  font-family: var(--font);
  font-size: 1.4rem;
  overflow-x: hidden;
  font-weight: 300;
}

img {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
}

input,
button,
textarea,
select {
  font-family: "Poppins", sans-serif;
  font-family: var(--font);
  font-size: 1.4rem;
  font-weight: 300;
  outline: none;
  border: 0;
  margin: 0;
  padding: 0;
  border-radius: 0;
  -webkit-appearance: none;
}

button {
  cursor: pointer;
}

.dashed-loading {
  position: relative;
  height: 50px;
  transform: translate(50%, 50%);
}
.dashed-loading:after,
.dashed-loading:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  width: 50px;
  height: 50px;
}
.dashed-loading:before {
  z-index: 5;
  border: 3px dashed #8be9fd;
  border: 3px dashed var(--blue);
  border-left: 3px solid transparent;
  border-bottom: 3px solid transparent;
  animation: dashed 1s linear infinite;
}
.dashed-loading:after {
  z-index: 10;
  border: 3px solid #8be9fd;
  border: 3px solid var(--blue);
  border-left: 3px solid transparent;
  border-bottom: 3px solid transparent;
  animation: dashed 1s ease infinite;
}
@keyframes dashed {
  to {
    transform: rotate(360deg);
  }
}

